@import "./styles/Header.scss";
@import "./styles/About.scss";
@import "./styles//Projects.scss";
@import "./styles/Settings.scss";

@import url("https://fonts.googleapis.com/css?family=Merriweather|Montserrat&display=swap");

.App {
  position: relative;
  width: 100%;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  margin: 0px;
  background-color: #fcede7;
  color: #5f4b8bff;
}
.home {
  height: 100vh;
  width: 100%;
  hr {
    width: 50vw;
    background-color: #e69a8dff;
    height: 3px;
    border-radius: 3px;
  }
}

.landing {
  margin: 0 25vh;
  padding: 30vh 0;
  box-sizing: border-box;
  h1 {
    font-size: 3em;
    font-family: "Merriweather", serif;
    animation: swoop 1s ease-in;
    color: #5f4b8bff;
  }
  p {
    font-size: 23px;
    margin-top: -35px;
  }
}
.options {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  a {
    margin: 0 10px;
    text-decoration: none;
    transition: transform 1s;
  }
  a:hover {
    transform: scale(1.2);
  }
}

@keyframes swoop {
  0% {
    transform: scale(1.1, 1.1) translateY(-150%);
  }
  100% {
    transform: scale(1, 1) translateY(0%);
  }
}
#up {
  position: fixed;
  bottom: 100px;
  right: 20px;
  @include tablet {
    display: none;
  }
}

footer {
  width: 100%;
  height: 10vh;
  // background-color:#D25565;
  background-color: #5f4b8bff;
  color: #e69a8dff;
  margin-top: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@include tablet {
  .landing {
    margin: 0 5vh;
    h1 {
      font-size: 28px;
      margin-bottom: 40px;
    }
    p {
      font-size: 20px;
    }
  }
}
@include sphone {
  .landing {
    h1 {
      font-size: 22px;
    }
    p {
      font-size: 18px;
    }
  }
}
